// src/pages/Train.js
import React from 'react';
import { Link } from 'react-router-dom';
import './Train.css';

const Train = () => (
  <div className="phone-frame">
    <div className="train-container">
      <h1 className="title"></h1>

      <div className="train-option">
        <div className="train-info">
          <div> T101</div>
          <div>: 13:00</div>
          <div>: 16:00</div>
        </div>
        <Link to="/checkout" className="select-button"></Link>
      </div>

      <div className="train-option">
        <div className="train-info">
          <div> T202</div>
          <div>: 17:00</div>
          <div>: 20:00</div>
        </div>
        <Link to="/checkout" className="select-button"></Link>
      </div>

      <div className="train-option">
        <div className="train-info">
          <div> T303</div>
          <div>: 18:30</div>
          <div>: 21:30</div>
        </div>
        <Link to="/checkout" className="select-button"></Link>
      </div>
    </div>
  </div>
);

export default Train;
